body {
  color: #1c1c1e;
  background-color: #f8f9fa;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

header {
  color: #fff;
  background-color: #2ecc71;
  justify-content: center;
  align-items: center;
  padding: 15px;
  font-family: Poppins, sans-serif;
  font-weight: 600;
  display: flex;
  position: relative;
}

header h1 {
  margin: 0;
}

.auth-form {
  text-align: center;
  background: #fff;
  border-radius: 12px;
  max-width: 400px;
  margin: 50px auto;
  padding: 25px;
  box-shadow: 0 4px 8px #0000001a;
}

.auth-form label {
  text-align: left;
  color: #333;
  margin: 10px 0 5px;
  font-size: 14px;
  font-weight: 500;
  display: block;
}

.auth-form input {
  box-sizing: border-box;
  border: 2px solid #3498db;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 15px;
  padding: 12px;
  font-family: Inter, sans-serif;
  font-size: 16px;
}

.auth-form input:focus {
  border-color: #2980b9;
  outline: none;
  box-shadow: 0 0 5px #2980b980;
}

.auth-form button {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 12px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 600;
  transition: background .3s ease-in-out;
}

.auth-form button:hover {
  background-color: #27ae60;
}

.auth-form button:active {
  background-color: #1e8449;
}

#toggleLink {
  margin-top: 15px;
}

#toggleLink a {
  color: #2ecc71;
  text-decoration: none;
}

#toggleLink a:hover {
  text-decoration: underline;
}

@media (width <= 480px) {
  .auth-form {
    width: 90%;
    padding: 20px;
  }
}

.habit-form, .habit-list {
  background: #fff;
  border-radius: 12px;
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  font-family: Poppins, sans-serif;
  box-shadow: 0 4px 8px #0000001a;
}

input[type="text"] {
  border: 2px solid #3498db;
  border-radius: 6px;
  width: 80%;
  margin: 10px 0;
  padding: 10px;
  font-family: Inter, sans-serif;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: 8px;
  padding: 10px 15px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 600;
  transition: background .3s ease-in-out;
}

button:hover {
  background-color: #27ae60;
}

button:active {
  background-color: #1e8449;
}

#signOutBtn {
  background-color: #ff4b5c;
  padding: 10px 15px;
  font-weight: 600;
  position: absolute;
  top: 15px;
  right: 20px;
}

#signOutBtn:hover {
  background-color: #d43f4a;
}

ul {
  padding: 0;
  list-style: none;
}

.habit-item {
  background-color: #f1f1f1;
  border-radius: 6px;
  flex-direction: column;
  align-items: flex-start;
  margin: 10px 0;
  padding: 20px;
  display: flex;
}

.habit-text {
  margin-bottom: 10px;
  font-size: 16px;
}

.habit-buttons {
  gap: 10px;
  display: flex;
}

.complete-btn, .delete-btn {
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
}

.complete-btn {
  color: #fff;
  background-color: #3498db;
}

.complete-btn:hover {
  background-color: #2980b9;
}

.delete-btn {
  color: #fff;
  background-color: #ff4b5c;
}

.delete-btn:hover {
  background-color: #d43f4a;
}

#chatbot-container {
  text-align: left;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 300px;
  padding: 15px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

.history {
  background-color: #f1f1f1;
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 10px;
}

#chat-input {
  border: 2px solid #3498db;
  border-radius: 6px;
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  background-color: #2ecc71;
  border-radius: 6px;
  padding: 5px 10px;
  font-family: Poppins, sans-serif;
}

#send-btn:hover {
  background-color: #27ae60;
}
